import 'babel-polyfill';
import m from 'mithril';
import moment from 'moment';
import 'moment-timezone';
import _ from 'lodash';
import decider from './decider';

const getQuestionnaire = async (q, a) => {
  const submission = await m.request({
    method: 'GET',
    headers: {
      auth: localStorage.getItem('token'),
    },
    url: `${window.env !== 'production' ? 'http://localhost:4000' : 'https://graph.braiven.io'}/submision/${a}`,
  });

  console.log({ submission });
  const apiData = await m.request({
    method: 'POST',
    url: window.env !== 'production' ? 'http://localhost:4000/' : 'https://graph.braiven.io',
    headers: {
      auth: localStorage.getItem('token'),
    },
    data: {
      query: `query($id:String!){
          questionnaire(id:$id){
            id,
            name,
            order,
            pages{
              id,
              name
              groups{
                id,
                name,
                questions{
                  id,
                  type,
                  contentType
                  placeholder,
                  tag,
                  position,
                  options{
                    label,
                    value
                  },
                  sentences{
                    sentenceLabel,
                    sentenceValue
                  }
                }
              }
            }
          }
        }`,
      variables: JSON.stringify({
        id: q,
      }),
    },
  });

  const { questionnaire } = apiData.data;

  return { questionnaire, submission };
};


const printer = {
  async oninit({ attrs, state }) {
    state.questionnaire = null;
    state.submission = {};
    console.log('fetching q');
    const { questionnaire, submission } = await getQuestionnaire(attrs.q, attrs.a);


    const questions = [];
    questionnaire.pages.map(page => page.groups.map((group) => {
      group.questions.map((question) => {
        question.group = group.id;

        if (question.options) question.options = question.options.map(({ label: olabel, value: ovalue }) => ({ olabel, ovalue }));

        if (question.sentences) question.sentences = question.options.map(({ sentence: osentence, value: ovalue }) => ({ osentence, ovalue }));

        if (questionnaire.order) {
          // console.log(question.id, project.questionnaire.order.indexOf(question.id))
          if (questionnaire.order.indexOf(question.id) !== -1) { return questions[questionnaire.order.indexOf(question.id)] = question; }

          // add it anyways
          if (questionnaire.order.indexOf(question.id) === -1) { questions.push(question); }
        } else {
          questions.push(question);
        }
      });
    }));

    Object.assign(state, {
      questions,
      questionnaire,
      submission,
    });
  },
  view({ state }) {
    const {
      __agentFirstName = '',
      completedAt,
      __timezone,
    } = state.submission;
    if (state.questions) {
      return m('div.k-invoice-v2',
        [
          m('div.k-invoice-v2__header',
            [
              m('div.k-invoice-v2__header-left',
                m('h1.k-invoice-v2__title', {
                  style: {
                    'font-size': '2rem',
                  },
                },
                state.questionnaire.name)),
              m('div.k-invoice-v2__header-right',
                [
                  m('div.k-invoice-v2__companyinfo',
                    [
                      `Filled by ${__agentFirstName}, ${moment(completedAt).tz(__timezone).format('LLL')}`,
                    ]),
                ]),
            ]),
          m('div.k-invoice-v2__line'),
          m('div.k-invoice-v2__body', {
            style: {
              'padding-top': '0px',
            },
          },
          m('div.row',
            [
              m('div.col-md-12',
                !state.questionnaire.order
                  ? _.sortBy(state.questionnaire.pages, 'name').map(page => [
                    _.sortBy(page.groups, 'name').map(group => ([
                      m('p', m('u', m.trust(group.name))),
                      _.sortBy(group.questions, 'position').map(question => m(decider, {
                        question,
                        fullSubmission: state.submission,
                        submission: state.submission[question.tag],
                      })),
                    ])),
                  ])
                  : state.questions.map(question => m(decider, { question, fullSubmission: state.submission, submission: state.submission[question.tag] }))),
            ])),
        ]);
    }
  },
};

m.route.prefix('');
m.route(document.getElementById('printer'), '/printable/questionnnaire/:q/answer/:a', {
  '/printable/questionnnaire/:q/answer/:a': printer,
});
