import m from "mithril";
import moment from "moment";
import map from "../saas/pages/home/components/google-maps";

export default {
  view({ attrs: { submission, question, fullSubmission } }) {
    // console.log(JSON.stringify(fullSubmission, null, '\t'));
    if (question.type === "instruction") {
      return [m("p", m.trust(question.placeholder))];
    }

    if (question.type === "time") {
      return [m("h5", moment(submission).format("LTS"))];
    }

    if (question.type === "date") {
      return [m("h5", moment(submission).format("LL"))];
    }

    if (question.type === "signature") {
      return [
        m("br"),
        m("img", {
          style: {
            height: "100",
            width: "300px"
          },
          src: submission
        }),
        m("br")
      ];
    }

    if (question.type === "cameraImage") {
      return [
        m("br"),
        m("img", {
          style: {
            width: "40%"
          },
          // fix for spaces in the aws url during construction
          src: submission
        }),
        m("br")
      ];
    }

    if (question.type === "input") {
      return [
        m("div.form-group", [
          m("label[for='exampleInputPassword1']", question.placeholder),
          m("input.form-control", {
            value: submission
          })
        ])
      ];
    }

    if (question.type === "GpsCoordinates") {
      return [
        m("br"),
        m(
          "div",
          {
            style: {
              width: "40%"
            }
          },
          [
            m(map, {
              markerSelected(meta) {
                console.log("marker selected", meta);
              },
              gpsData: [
                [fullSubmission].map(
                  ({ _id, GPS_latitude: lat, GPS_longitude: long }) => ({
                    lat,
                    long,
                    meta: _id
                  })
                )
              ]
            })
          ]
        ),
        m("br")
      ];
    }

    if (question.type === "multipleSelect") {
      return m(".form-group", [
        m(".k-checkbox-list", [
          question.options.map(op =>
            m("label.k-checkbox", [
              // console.log('searching for ', `${question.tag}_option_${op.olabel.replace(/\s/g, '_')}`),
              m("input[type='checkbox']", {
                checked:
                  fullSubmission[
                    `${question.tag}_option_${op.olabel.replace(/\s/g, "_")}`
                  ] === op.ovalue
              }),
              op.olabel,
              m("span")
            ])
          ),
          m("br")
        ])
      ]);
    }

    if (question.type === "singleSelect") {
      return [
        m("div.form-group", [
          m("div.k-radio-list", [
            question.options.map(op =>
              m("label.k-radio", [
                m("input[type='radio']", {
                  checked:
                    fullSubmission[
                      `${question.tag}_${op.olabel.replace(/\s/g, "_")}`
                    ] === op.ovalue,
                  name: question.tag
                }),
                op.olabel,
                m("span")
              ])
            )
          ])
        ]),
        m("br")
      ];
    }

    if (question.type === "file") {
      return [
        m("br"),
        m(
          `a[href='${submission}'][download='${submission}']`,
          "Download file here"
        ),
        m("br")
      ];
    }

    return m(
      "p",
      { style: { color: "red" } },
      `${question.type} is not suppored yet`
    );
  }
};
